import 'babel-polyfill'
import React, { Component } from 'react'
import { connect } from 'react-redux'

import { store } from '../store'
import ChangeFriend from '../components/ChangeFriend'
import ChatHistory from '../components/ChatHistory'
import ChatInput from '../components/ChatInput'
import * as actionTypes from '../constants/actionTypes'

class App extends Component {
  componentWillMount() {
    store.dispatch({ type: actionTypes.FETCH_WEB3_CONNECTION_REQUESTED })
  }

  render() {
    return (
      <div className="App">
        <ChangeFriend changeFriend={ this.props.changeFriend } fetchHistory={ this.props.fetchHistory } />
        <ChatHistory history={ this.props.history } />
        <ChatInput sendMessage={ this.props.sendMessage } userAddress={ this.props.userAddress } friendAddress={ this.props.friendAddress } />
      </div>
    );
  }
}

function mapDispatchToProps(dispatch) {
    return {
        fetchHistory: () => {
            dispatch({ type: actionTypes.FETCH_HISTORY_REQUESTED })
        },
        sendMessage: (msg) => {
            dispatch({ type: actionTypes.SEND_MESSAGE_REQUESTED, payload: msg })
        },
        changeFriend: (address) => {
            dispatch({ type: actionTypes.SET_FRIEND_ADDRESS, payload: address })
            dispatch({ type: actionTypes.FETCH_HISTORY_REQUESTED})
        }
    }
}

export default connect(
    state => state,
    mapDispatchToProps,
)(App);
